import React from 'react';
import propTypes from 'prop-types';
import {Accordion, Card} from 'react-bootstrap';
import { Navigate } from 'react-router-dom';


class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            p1: true,
            p2: false,
            p3: false,
            navigate: false
        }
    }

    oneClick = () => {
        const open = this.state.p1;
        open
            ? this.setState({
                p1: false,
                p2: false,
                p3: false,
                navigate: false
            })
            : this.setState({
                p1: true,
                p2: false,
                p3: false,
                navigate: false
            })
    }

    twoClick = () => {
        const open = this.state.p2;
        open
            ? this.setState({
                p1: false,
                p2: false,
                p3: false,
                navigate: false
            })
            : this.setState({
                p1: false,
                p2: true,
                p3: false,
                navigate: false
            })
    }

    threeClick = () => {
        const open = this.state.p3;
        open
            ? this.setState({
                p1: false,
                p2: false,
                p3: false,
                navigate: false
            })
            : this.setState({
                p1: false,
                p2: false,
                p3: true,
                navigate: false
            })
    }

    render() {
        return (
            <div id="about-page">
                <header id="about-title-card">
                    <div id="about-back">
                        <button type="button" id="main-1" className="back" onClick={() => this.setState({navigate: true})}><i className="fas fa-angle-left"></i> Menu</button>
                    </div>
                    <h1 id="about-title">About Me</h1>
                </header>
                <div id="about-main-card">
                    <Accordion defaultActiveKey="0" id="about-text">

                        <Card className="about-card shadow">
                            <Accordion.Toggle as={Card.Header} eventKey="0" className="about-header" onClick={this.oneClick}>
                                <h2>Full Stack Software Engineer</h2>
                                <button type="button">{this.state.p1 ? "-" : "+"}</button>
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                            <Card.Body className="about-paragraph">
                                <p>Welcome to my page! My name is Austin, and I like learning. I like learning so much, in fact, that I am like a gentle river, flowing from ecosystem to ecosystem and carrying new knowledge along with me...but also like GODZILLA, bombarding new fields and using my learning-claws to dismantle them until I am their ruler! Ahem. Suffice it to say, I am a jack of all trades (and don't let the adage fool you, I am a master of them too!). If you've made it this far, it's likely you want to know more about my experience in one of said trades. So to help you in your quest, I will grant you what you seek; here is the no-frills, all-facts rundown of Austin Knauer. I am a:</p>
                                <ul>
                                    <li>Flexible software engineer well versed in creating full stack web applications using TypeScript, Node, and Java.</li>
                                    <li>Front end warrior with years of experience building single page React applications and 3 certifications covering many front end technologies and libraries.</li>
                                    <li>An insatiably curious and indefatigable worker who brings passion to everything I do. I tackle any and every challenge and always come out on top. I am the worker and collaborator you can depend on to always return stellar results.</li>
                                </ul>
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>

                        <Card id="card-2" className="shadow">
                            <Accordion.Toggle as={Card.Header} eventKey="1" className="about-header" onClick={this.twoClick}>
                                <h2>Fast Facts...</h2>
                                <button type="button">{this.state.p2 ? "-" : "+"}</button>
                            </Accordion.Toggle>

                            <Accordion.Collapse eventKey="1">
                                <Card.Body className="about-paragraph">
                                <ul>
                                    <li>I have certifications in Responsive Web Design, JavaScript Algorithms and Data Structures, and Front End Libraries.</li>
                                    <li>I have a BA in Liberal Arts with minors in English and History from Colorado State University, where I was nominated as a University Honors Scholar.</li>
                                    <li>My work is carefully attentive to both responsive design principles and accessibility.</li>
                                </ul>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>

                        <Card className="shadow">
                            <Accordion.Toggle as={Card.Header} eventKey="2" className="about-header" onClick={this.threeClick}>
                                <h2>Got your attention?</h2>
                                <button type="button" >{this.state.p3 ? "-" : "+"}</button>
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="2">
                            <Card.Body className="about-paragraph">
                                <p>Check out some of my work on the Portfolio page. Or, if you want a more detailed breakdown of my experience, download my Resume. If you think we'd be a good fit, you can find a way to reach me on the Contact page.</p>
                            </Card.Body>
                            </Accordion.Collapse>
                            




                        </Card>

                    </Accordion>

                </div>


                {this.state.navigate && <Navigate to="/" /> }
            </div>
        )
    }
}

About.propTypes = { handleClick: propTypes.func.isRequired };

export default About;
