import React, { Component } from 'react';
import propTypes from 'prop-types';
import resume from '../static assets/Austin Knauer Z.pdf';
import { Link } from 'react-router-dom';

class Main extends Component {
    render() {
        return (
            <div id="main-page">
                <header id="name-card">
                    <h1 id="name-title">AUSTIN<br />KNAUER</h1>
                </header>
                <nav id="button-card">
                    <Link to="/about" className="links">About Me</Link>
                    <Link to="/portfolio" className="links">Portfolio</Link>
                    <Link to="contact" className="links">Contact</Link>
                    <a href={resume} download="Austin Knauer Resume" className="links" id="resume">Resume</a>
                </nav>
            </div>
        )
    }
}

Main.propTypes = {handleClick: propTypes.func.isRequired};

export default Main
