import React, { Component } from 'react';
import propTypes from 'prop-types';
import { Navigate } from 'react-router-dom';

class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            navigate: false
        }
    }

    render() {
        return (
            <div id="contact-page">
                <nav id="contact-main-card">
                    <ul className="list-unstyled" id="contact-links">
                    <li className="media shadow">
                        <a href="http://linkedin.com/in/austin-knauer000" id="linkedin" className="contact-link align-self-center mr-3"><i className="fab fa-linkedin-in"></i></a>
                        <div className="media-body">
                        <h2 className="link-header">LinkedIn</h2>
                        <p>http://linkedin.com/in/austin-knauer000</p>
                        </div>
                    </li>
                    <li className="media shadow">
                        <a href="tel:1-720-989-7563" id="phone" className="contact-link align-self-center mr-3"><i className="fas fa-phone"></i></a>
                        <div className="media-body">
                        <h2 className="link-header">Phone</h2>
                        <p>720-989-7563</p>
                        </div>
                        
                    </li>
                    <li className="media shadow">
                        <a href="mailto:austinknauer@alumni.colostate.edu" id="email" className="contact-link align-self-center mr-3"><i className="fas fa-at"></i></a>
                        <div className="media-body">
                        <h2 className="link-header">Email</h2>
                        <p>austinknauer@alumni.colostate.edu</p>
                        </div>
                        
                    </li>
                    <li className="media shadow">
                        <a href="http://github.com/austin-knauer" id="github" className="contact-link align-self-center mr-3"><i className="fab fa-github"></i></a>
                        <div className="media-body">
                        <h2 className="link-header">Github</h2>
                        <p>http://github.com/austin-knauer</p>
                        </div>
                        
                    </li>
                    </ul>
                </nav>
                <header id="contact-title-card">
                    <div id="contact-back">
                        <button type="button" id="main-3" className="back" onClick={() => this.setState({navigate: true})}><i className="fas fa-angle-right"></i> Menu</button>
                    </div>
                    <h1 id="contact-title">Con<wbr></wbr>tact</h1>
                    {this.state.navigate && <Navigate to="/" /> }
                </header>
            </div>
        )
    }
}

Contact.propTypes = {handleClick: propTypes.func.isRequired};

export default Contact
